.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 20px;
  background-color: #f5f5f5;
  box-sizing: border-box;

  .logo {
    display: flex;
    align-items: center;
 
    
 

    img {
      height: 50px;
      margin-right: 10px;
    }
  }

  .nav-menu,
  .mobile-menu {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    flex: 1;
    justify-content: flex-end;
    flex-direction: row;

    li {
      margin: 0 5px;
      position: relative;

      a {
        padding: 10px 20px;
        background: #e0e0e0;
        color: #333;
        text-decoration: none;
        font-size: 0.9rem;
        font-weight: bold;
        text-align: center;
        transition: color 0.3s, background 0.3s;
        display: block;
        clip-path: polygon(10% 0, 100% 0, 90% 100%, 0 100%);

        &:hover {
          color: #fff;
          background: #02aef5;
        }

        &::before {
          content: "";
          position: absolute;
          top: 100%;
          left: 0;
          width: 100%;
          height: 100%;
          background: #02aef5;
          clip-path: polygon(10% 0, 100% 0, 90% 100%, 0 100%);
          transition: top 0.3s;
          z-index: -1;
        }

        &:hover::before {
          top: 0;
        }
      }

      &.active a {
        color: #fff;
        background: #02aef5;

        &::before {
          top: 0;
        }
      }

      &:last-child a {
        clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
      }

      // Dropdown menu
      &:hover .dropdown {
        display: block;
      }

      .dropdown {
        display: none;
        position: absolute;
        top: 98%;
        left: 0;
        background: #fff;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        z-index: 900;

        a {
          padding: 14px 20px;
          font-size: 0.8rem;
          background: #fff;
          color: #333;
          text-decoration: none;
          display: block;

          &:hover {
            background: #02aef5;
            color: #fff;
          }
        }
      }
    }
  }


  @media (max-width: 768px) {
    .nav-menu {
      display: none;
    }

    .menu-toggle {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      padding: 8px;
      border: none;
      background: none;
      cursor: pointer;
    }

    .mobile-menu {
      display: none;
      flex-direction: column;
      list-style: none;
      margin: 0;
      padding: 0;
      background-color: #f5f5f5;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
      transition: transform 0.3s ease-in-out;
      transform: translateY(-100%);
    }

    .mobile-menu.open {
      display: flex;
      transform: translateY(0);
    }

    .mobile-menu li {
      width: 100%;
      text-align: center;
      padding: 10px 0;

      a {
        padding: 10px;
        font-size: 1rem;
        background: #e0e0e0;
        color: #333;
        text-decoration: none;
        display: block;
        transition: color 0.3s, background 0.3s;

        &:hover {
          color: #fff;
          background: #02aef5;
        }
      }
    }

    .mobile-menu-backdrop {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      z-index: 50;
    }

    .mobile-menu-backdrop.open {
      display: block;
    }
  }

  @media (min-width: 769px) {
    .menu-toggle {
      display: none;
    }

    .mobile-menu {
      display: none;
    }
  }
}

