.team-section {
  padding: 100px;
  background-color: #b5b5b53e;
  text-align: center;

  .section-title {
    font-size: 2.5rem;
    color: #1f008e;
    margin-bottom: 20px;
  }

  .team-description {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 40px;
    line-height: 1.8;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }

  .managing-card {
    margin: 0 auto 30px;
    max-width: 70%;
    background: #fff;
    border-radius: 15px;
    padding: 25px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;

    .img-wrapper {
      flex: 1;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.3s ease-in-out;
      }
    }

    .caption {
      padding-left: 40px;
      flex: 2;
      text-align: left;
      h3 {
        font-size: 1.5rem;
        color: #333;
        margin-bottom: 10px;
      }
      p {
        font-size: 1rem;
        color: #777;
        margin-bottom: 10px;
      }
      .description {
        font-size: 0.9rem;
        color: #555;
        line-height: 1.6;
      }
    }
  }

  .team-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;

    .profile-card {
      width: 90%;
      max-width: 260px;
      background: #fff;
      border-radius: 10px;
      padding: 20px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      transition: all 0.3s ease-in-out;

      &:hover {
        box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
        transform: translateY(-5px);
      }

      .img-wrapper {
        overflow: hidden;
        border-radius: 50%;
        img {
          width: 100%;
          border-radius: 50%;
          transition: transform 0.3s;
        }
        &:hover img {
          transform: scale(1.1);
        }
      }

      .caption {
        text-align: center;

        h3 {
          font-size: 1.25rem;
          margin-bottom: 10px;
          color: #333;
        }

        p {
          font-size: 1rem;
          color: #555;
          margin-bottom: 10px;
        }

        .description {
          font-size: 0.9rem;
          color: #555;
          line-height: 1.6;
        }

        .social-links a {
          color: #333;
          font-size: 1.2rem;
          margin: 0 10px;
          &:hover {
            color: #0c52a1;
          }
        }
      }
    }
  }

  @media (max-width: 1024px) {
    padding: 80px 20px;

    .managing-card {
      flex-direction: column;
      align-items: center;
      text-align: center;

      .img-wrapper {
        margin-bottom: 20px;
      }

      .caption {
        padding-left: 0;
      }
    }

    .profile-card {
      width: 220px;
    }
  }

  @media (max-width: 768px) {
    padding: 60px 15px;

    .section-title {
      font-size: 2rem;
    }

    .profile-card {
      width: 180px;
    }
  }

  @media (max-width: 480px) {
    padding: 40px 10px;

    .section-title {
      font-size: 1.75rem;
    }

    .profile-card {
      width: 100%;
      padding: 15px;
    }
  }
}