.slider-container {
    width: 100%;
    overflow: hidden; // Ensure the slider container does not overflow
  }
  
  .relative {
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden; // Ensure each slide does not overflow
  }
  
  img {
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover; // Ensure the image covers the container without overflow
  }
  
  .absolute {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .absolute:hover {
    opacity: 1;
  }