// Variables 
$bg-color: #f1f3f5;
$primary-color: rgb(241, 238, 244);
$text-color: #090101;

// Base styles
html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background: $bg-color;
  color: $text-color;
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.slider-container{
  margin-top: 140px;
}


// Contact Page Layout
.row {
    margin: 100px;
  display: flex;
  flex-wrap: nowrap;
  height: 100%;

  #left,
  #right {
    flex: 1;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
  }
}

// Form Section
h1 {
  font-size: 2.5rem;
  text-align: center;
  color: #38b6ff;
  margin-bottom: 20px;
  animation: fadeInDown 1s ease-in-out;
}

.formbox {
  background: transparent;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 4px 10px 2px rgba(0, 0, 0, 0.2);

  form {
    display: flex;
    flex-direction: column;
    gap: 15px;

    input,
    textarea {
      font-size: 1rem;
      padding: 10px;
      border: 2px solid $primary-color;
      border-radius: 5px;
      background: transparent;
      color: $text-color;
      transition: border-color 0.3s;

      &:focus {
        border-color: $primary-color;
        outline: none;
      }
    }

    .submit-button {
      align-self: flex-end;
      width: 120px;
      padding: 10px;
      font-weight: bold;
      background: #38b6ff;
      color: $primary-color;
      border: 2px solid $primary-color;
      border-radius: 5px;
      cursor: pointer;
      transition: all 0.3s;

      &:hover {
        background: White;
        color: #38b6ff;
        transform: scale(1.1);
      }
    }
  }
}

// Map Section
#right {
  position: relative;

  iframe {
    width: 100%;
    height: 100%;
    border: none;
  }

  #map-overlap {
    position: absolute;
    top: 100px;
    left: 20px;
    height: 116%;
    background: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 4px 10px 2px rgba(0, 0, 0, 0.2);
    color: #000;

    h2 {
      font-size: 2rem;
      color: #38b6ff;
    }

    p {
      margin: 10px 0;
    }
  }
}

// Animations
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// Responsive Design
@media (max-width: 1200px) {
  .row {
    margin: 50px;

    #left,
    #right {
      padding: 15px;
    }
  }

  h1 {
    font-size: 2rem;
  }

  .formbox {
    form {
      input,
      textarea {
        font-size: 0.9rem;
        padding: 8px;
      }

      .submit-button {
        width: 100px;
        padding: 8px;
      }
    }
  }

  #map-overlap {
    padding: 15px;
  }
}

@media (max-width: 992px) {
  .row {
    flex-direction: column;
    margin: 30px;

    #left,
    #right {
      min-width: 100%;
      height: auto;
      padding: 10px;
    }
  }

  h1 {
    font-size: 1.8rem;
  }

  #map-overlap {
    position: relative;
    margin: 10px auto;
    top: 0;
    left: 0;
    width: auto;
    text-align: center;
  }
}

@media (max-width: 768px) {
  .row {
    flex-direction: column;
    margin: 20px;

    #left,
    #right {
      min-width: 100%;
      height: auto;
    }
  }

  h1 {
    font-size: 1.5rem;
  }

  .formbox {
    form {
      input,
      textarea {
        font-size: 0.8rem;
        padding: 6px;
      }

      .submit-button {
        width: 90px;
        padding: 6px;
      }
    }
  }

  #map-overlap {
    margin: 10px;
    padding: 10px;
  }
}

@media (max-width: 576px) {
  .row {
    margin: 150px 10px;
   

    #left,
    #right {
      padding: 5px;
    }
  }

  h1 {
    font-size: 1.2rem;
  }

  .formbox {
    form {
      input,
      textarea {
        font-size: 0.7rem;
        padding: 5px;
      }

      .submit-button {
        width: 80px;
        padding: 5px;
      }
    }
  }

  #map-overlap {
    margin: 5px;
    padding: 5px;
  }
}