.about-slider {

    padding-top: 120px;
      .slider-container {
      
        position: relative;
        width: 100%;
        margin: 0 auto;
      }
  
      .relative {
        position: relative;
      }
    
      img {
        width: 100%;
        height: auto;
        border-radius: 10px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      }
    
      .absolute {
        position: absolute;
        inset: 0;
        background-color: rgba(0, 0, 0, 0.3);
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transition: opacity 0.3s ease;
      }
    
      .absolute:hover {
        opacity: 1;
      }
    
      .text-center {
        text-align: center;
      }
    
      .text-white {
        color: white;
      }
    
      .bg-blue-500 {
        background-color: #3b82f6;
      }
    
      .bg-green-500 {
        background-color: #10b981;
      }
    
      .hover\:bg-blue-700:hover {
        background-color: #1d4ed8;
      }
    
      .hover\:bg-green-700:hover {
        background-color: #047857;
      }
    
      .px-4 {
        padding-left: 1rem;
        padding-right: 1rem;
      }
    
      .py-2 {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
      }
    
      .rounded {
        border-radius: 0.375rem;
      }
    
      .ml-4 {
        margin-left: 1rem;
      }
    
      .transition {
        transition: background-color 0.3s ease;
      }
    }